@use '../base/variables' as v; 


.no-webp .imagen-contacto{
    background-image: url('../img/encuentra.jpg');
}

.webp .imagen-contacto{
    background-image: url('../img/encuentra.avif');
}

.imagen-contacto{
    background-position: center center;
    background-size: cover;
    padding: v.$separacion * 2 0;
    display: flex;
    flex-direction: column;
    align-items: center;

    h2{
        font-size: 4rem;
        font-weight: v.$black;
        color: v.$gris;
    }
    p{
        font-size: 1.8rem;
        color: v.$gris;
    }
}